* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
:root {
  --primary-color: #189ab4;
  --error-color: #ff0000;
  --text-color: #fff;
  --white-color: #fff;
  --black-color: rgb(0, 0, 0);
  --base-color: #d5ffeed2;
}
.App {
  text-align: center;
  height: 100vh;
  width: 35rem;
  padding: 1rem;
  position: relative;
}
-webkit-scrollbar {
  width: 6px;
  background-color: var(--primary-color);
}
.routes-cont {
  width: 100%;
  height: 3rem;
  justify-content: space-between;
  margin-bottom: 1rem;
  border-radius: 5px;
  color: var(--text-color);
  display: flex;
  align-items: center;
}
.route {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 49%;
  border-radius: 5px;
  background-color: var(--primary-color);
  border: 2px solid var(--primary-color);
  text-transform: uppercase;
  letter-spacing: 2px;
  cursor: pointer;
  transition: all 0.5s;
}
.route:hover {
  background-color: var(--text-color);
  border: 2px solid var(--primary-color);
  color: var(--primary-color);
}
.sec-route {
  background-color: var(--text-color);
  border: 2px solid var(--primary-color);
  color: var(--primary-color);
}
.sec-route:hover {
  background-color: #3f6e3f;
  color: var(--text-color);
}
