.get-api-data {
  width: 100%;
  height: 3rem;
  display: flex;
  align-items: center;
  color: var(--primary-color);
  justify-content: space-between;
}

.get-api-data select {
  width: 5.5rem;
  height: 80%;
  padding: 10px 10px;
  border: none;
  border: 2px solid var(--primary-color);
  border-radius: 5px;
  letter-spacing: 1px;
  color: var(--primary-color);
  font-weight: 600;
}

.get-api-data input {
  height: 80%;
  width: 55%;
  letter-spacing: 2px;
  border: 2px solid var(--primary-color);
  border-radius: 5px;
  padding: 0 10px;
  color: var(--primary-color);
}

.get-api-data button {
  color: var(--text-color);
  background-color: var(--primary-color);
  height: 80%;
  width: 5rem;
  text-transform: uppercase;
  letter-spacing: 1px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.header-container {
  width: 100%;
  height: 3rem;
  color: var(--primary-color);
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 1rem;
}

.header-container input {
  height: 70%;
  width: 45%;
  letter-spacing: 2px;
  border: 2px solid var(--primary-color);
  border-radius: 5px;
  padding: 0 10px;
  color: var(--primary-color);
  outline: none;
}

.body-sec {
  width: 100%;
  margin-top: 1rem;
  display: flex;
  flex-direction: column;
}

.body-sec textarea {
  height: 10rem;
  width: 100%;
  border: 2px solid var(--primary-color);
  resize: none;
  padding: 1rem;
  overflow: auto;
  outline: none;
}

.json-err {
  color: var(--error-color);
  text-align: start;
  padding-top: 1rem;
}

.response-cont {
  background-color: var(--base-color);
  width: 100%;
  height: 12.5rem;
  border-radius: 5px;
  padding: 1rem;
  overflow: auto;
  margin-top: 1rem;
}

.response-cont::-webkit-scrollbar {
  width: 6px;
  background-color: var(--primary-color);
}

h2 {
  font-size: 0.8rem;
  text-transform: uppercase;
  letter-spacing: 1px;
  color: var(--primary-color);
  width: 6rem;
  text-align: start;
}

.response-data-sec {
  display: flex;
  justify-content: space-between;
}

.json-response {
  width: 27rem;
  padding: 1rem;
}

.stackoverflow-cont {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 10rem;
  padding: 1rem;
  justify-content: space-between;
  margin-top: 1rem;
}

.stackoverflow-cont input {
  height: 3rem;
  width: 85%;
  letter-spacing: 2px;
  border: 2px solid var(--primary-color);
  border-radius: 5px;
  padding: 0 10px;
  color: var(--primary-color);
  outline: none;
}

.stackoverflow-cont button {
  color: var(--text-color);
  background-color: var(--primary-color);
  height: 2.8rem;
  width: 85%;
  text-transform: uppercase;
  letter-spacing: 2px;
  font-weight: 600;
  font-size: 1.3rem;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.stack-img {
  width: 100%;
}

.stack-img img {
  height: 100%;
  width: 100%;
  margin-top: 1rem;
}

.time {
  font-size: 2rem;
  letter-spacing: 2px;
  text-align: center;
  margin: 1rem 0;
  position: relative;
  height: 10rem;
  color: var(--white-color);
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 600;
  text-shadow: 2px 3px var(--black-color);
  background-color: var(--base-color);
  background: url(https://wallpaperaccess.com/full/435558.jpg) center
    center/cover;
  border-radius: 5px;
  overflow: hidden;
}

.protips {
  width: 100%;
  padding: 1rem;
  background-color: var(--base-color);
  display: flex;
  align-items: center;
  border-radius: 5px;
}
.tips {
  width: 30%;
  color: var(--primary-color);
  font-weight: 600;
}
.snowflake {
  color: var(--white-color);
  font-size: 1em;
  font-family: Arial;
  text-shadow: 0 0 1px #000;
}

.footer {
  display: flex;
  justify-content: space-around;
  color: var(--primary-color);
  font-weight: 700;
  padding: 1rem 0;
  cursor: pointer;
}
a {
  color: var(--primary-color);
  text-decoration: none;
}

@-webkit-keyframes snowflakes-fall {
  0% {
    top: -10%;
  }

  100% {
    top: 100%;
  }
}

@-webkit-keyframes snowflakes-shake {
  0% {
    -webkit-transform: translateX(0px);
    transform: translateX(0px);
  }

  50% {
    -webkit-transform: translateX(80px);
    transform: translateX(80px);
  }

  100% {
    -webkit-transform: translateX(0px);
    transform: translateX(0px);
  }
}

@keyframes snowflakes-fall {
  0% {
    top: -10%;
  }

  100% {
    top: 100%;
  }
}

@keyframes snowflakes-shake {
  0% {
    transform: translateX(0px);
  }

  50% {
    transform: translateX(80px);
  }

  100% {
    transform: translateX(0px);
  }
}

.snowflake {
  position: fixed;
  top: -10%;
  z-index: 9999;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  cursor: default;
  -webkit-animation-name: snowflakes-fall, snowflakes-shake;
  -webkit-animation-duration: 10s, 3s;
  -webkit-animation-timing-function: linear, ease-in-out;
  -webkit-animation-iteration-count: infinite, infinite;
  -webkit-animation-play-state: running, running;
  animation-name: snowflakes-fall, snowflakes-shake;
  animation-duration: 10s, 3s;
  animation-timing-function: linear, ease-in-out;
  animation-iteration-count: infinite, infinite;
  animation-play-state: running, running;
}

.snowflake:nth-of-type(0) {
  left: 1%;
  -webkit-animation-delay: 0s, 0s;
  animation-delay: 0s, 0s;
}

.snowflake:nth-of-type(1) {
  left: 10%;
  -webkit-animation-delay: 1s, 1s;
  animation-delay: 1s, 1s;
}

.snowflake:nth-of-type(2) {
  left: 20%;
  -webkit-animation-delay: 6s, 0.5s;
  animation-delay: 6s, 0.5s;
}

.snowflake:nth-of-type(3) {
  left: 30%;
  -webkit-animation-delay: 4s, 2s;
  animation-delay: 4s, 2s;
}

.snowflake:nth-of-type(4) {
  left: 40%;
  -webkit-animation-delay: 2s, 2s;
  animation-delay: 2s, 2s;
}

.snowflake:nth-of-type(5) {
  left: 50%;
  -webkit-animation-delay: 8s, 3s;
  animation-delay: 8s, 3s;
}

.snowflake:nth-of-type(6) {
  left: 60%;
  -webkit-animation-delay: 6s, 2s;
  animation-delay: 6s, 2s;
}

.snowflake:nth-of-type(7) {
  left: 70%;
  -webkit-animation-delay: 2.5s, 1s;
  animation-delay: 2.5s, 1s;
}

.snowflake:nth-of-type(8) {
  left: 80%;
  -webkit-animation-delay: 1s, 0s;
  animation-delay: 1s, 0s;
}

.snowflake:nth-of-type(9) {
  left: 90%;
  -webkit-animation-delay: 3s, 1.5s;
  animation-delay: 3s, 1.5s;
}
